/* Ð¡ÑÐ²Ð¾ÑÐ¸ÑÐ¸ Ð¼ÑÑÑÐµ Ð´Ð»Ñ ÑÑÑÑÑÐºÐ¸ Ð¿ÑÐ¾ÐºÑÑÑÐºÐ¸ Ð·Ð° Ð´Ð¾Ð¿Ð¾Ð¼Ð¾Ð³Ð¾Ñ Ð¿ÑÐµÐ²Ð´Ð¾ÐºÐ»Ð°ÑÑ */
.transaction-list::after {
